  //  margin-top: -$footer-height;
  //  z-index:9999;
  //background-color:rgba(0,0,0,0.90);
}

// Styling for mobile devices
@media screen and (max-width: 768px) {
  .site-footer, .wrap:after {
    height: $footer-height * 2.8;
  }

  .recent-posts ul li {
    margin-bottom: 2em;
  }
}
